import { all } from 'redux-saga/effects';
import loginSaga from './loginSaga';
import registrationSaga from './registrationSaga';
import userSaga from './userSaga';
import collectionSaga from './collectionSaga';
import brandSaga from './brandSaga';
import colorSaga from './colorSaga';
import shoeSaga from './shoeSaga';
import dateSaga from './dateSaga';
import deleteShoeSaga from './deleteShoeSaga';
import image from './AddImgSaga';
import addShoe from './addShoeSaga';

// rootSaga is the primary saga.
// It bundles up all of the other sagas so our project can use them.
// This is imported in index.js as rootSaga

// some sagas trigger other sagas, as an example
// the registration triggers a login
// and login triggers setting the user
export default function* rootSaga() {
  yield all([
    loginSaga(),
    registrationSaga(),
    userSaga(),
    collectionSaga(),
    brandSaga(),
    colorSaga(),
    shoeSaga(),
    dateSaga(),
    deleteShoeSaga(),
    image(),
    addShoe(),
  ]);
}
